import React from "react";
import s from "./ModalSignIn.module.css";
import styles from "../../../../../App.module.css";
import close from "../../../../../assets/icons/close.svg"
import FB from "../../../../../assets/icons/modalFacebook.svg"
import Google from "../../../../../assets/icons/ModalGoogle.svg"
import Apple from "../../../../../assets/icons/ModalApple.svg"
import Mail from "../../../../../assets/icons/ModalMail.png"


const Modal = ({toggleModalSignIn, auth, user, updateInput}) => {
    return (
        <div className={s.modal}>
            <div className={s.overlay} >
                <div className={s.modalContent}>
                    <div className={s.modalText}>
                        <p className={s.welcome}>   KONOO!</p>

                        <input className={s.modalInput} type="text" placeholder="" name="username" required
                            value={user.username ?? ""} onChange={updateInput} />

                        <input className={s.modalInput} type="password" placeholder="" name="password" required
                            value={user.password ?? ""} onChange={updateInput} />

                        <p className={s.modalText}>     SMS,    . <br />
                                      . <br />
                             </p>
                        <button className={`${styles.primaryBtn} ${s.modalBtn}`}
                            onClick={auth}></button>

                        <p></p>

                        <button className={s.signWithBtn} onClick={e => toggleModalSignIn(e)}>
                            <img className={s.signWithIcon} src={FB} alt="facebookIcon" />
                            <a className={s.signWithLink} href="#">  Facebook</a>
                        </button>
                        <button className={s.signWithBtn} onClick={e => toggleModalSignIn(e)}>
                            <img className={s.signWithIcon} src={Google} alt="GoogleIcon" />
                            <a className={s.signWithLink} href="#">  Google</a>
                        </button>
                        <button className={s.signWithBtn} onClick={e => toggleModalSignIn(e)}>
                            <img className={s.signWithIcon} src={Apple} alt="AppleIcon" />
                            <a className={s.signWithLink} href="#">  Apple</a>
                        </button>
                        <button className={s.signWithBtn} onClick={e => toggleModalSignIn(e)}>
                            <img className={s.signWithIcon} src={Mail} alt="MailIcon" />
                            <a className={s.signWithLink} href="#">  .</a>
                        </button>
                    </div>
                    <div className={s.closeModal} onClick={e => toggleModalSignIn(e)}><img src={close} alt="closeMark" /></div>
                </div>
            </div>
        </div>
    )
}

export default Modal;